import 'uni-pages';
// @ts-ignore
wx.__webpack_require_UNI_MP_PLUGIN__ = __webpack_require__;import App from './App'
import messages from './locale/index'
import store from './store/index.js'

let i18nConfig = {
	locale: uni.getLocale(),
	messages
}

const msgError = (msg = '提示信息', duration = 3000, type = 'fail') => {
	uni.showToast({
		title: msg,
		type,
		duration,
	})
}

const msgSuccess = (msg = '提示信息', duration = 3000, type = 'success') => {
	uni.showToast({
		title: msg,
		type,
		duration,
	})
}

const showLongMsg = (msg = '提示信息', duration = 3000, type = 'none') => {
	uni.showToast({
		title: msg,
		type,
		duration,
	})
}


import Vue from 'vue'
import VueI18n from 'vue-i18n'
Vue.use(VueI18n)
const i18n = new VueI18n(i18nConfig)
Vue.config.productionTip = false
App.mpType = 'app'
Vue.prototype.$store = store
Vue.prototype.$msgError = msgError
Vue.prototype.$msgSuccess = msgSuccess
Vue.prototype.$showLongMsg = showLongMsg

const app = new Vue({
	i18n,
	store,
	...App
})
app.$mount()